import Vue from 'vue'
import Router from 'vue-router'
// import RoomsList from '@/views/RoomsList'
// import Index from '@/views/Index'

const Index = resolve => require(['@/views/Index.vue'], resolve)
const RoomsList = resolve => require(['@/views/RoomsList.vue'], resolve)

Vue.use(Router)


export default new Router({
  mode:'history',
  routes: [
    {
      path: '/',
      name: 'RoomsList',
      component: RoomsList
    },
    {
      path: '/info',
      name: 'Index',
      component: Index
    },

  ]
})



